import React from 'react'
import Rbc from 'react-beautiful-calendar'

import GithubLogo from './assets/github-logo.svg'
import styles from './App.module.css'

const App = () => {
  const dateChangeHandler = ([date, month, year]) => {
    console.log(`Date: ${date}-${month}-${year}`)
  }

  return (
    <div className={styles.AppWrapper}>
      <Rbc onDateChange={dateChangeHandler}/>
      <div className={styles.Header}>
        <h1>react-beautiful-calendar</h1>
        <p>A beautiful calendar library for your beautiful react applications.</p>
        <br/>
        <a href="https://github.com/daxter-army/react-beautiful-calendar/" className={styles.ImageLink}>
          <img src={GithubLogo} alt="github-logo" className={styles.GithubImage}/><span className={styles.NameTag}>Mehul Singh Teya</span>
        </a>
        <br/><br/><br/>
        <p><b>Coming Soon!</b></p>
      </div>
    </div>
  )
}

export default App